import React from 'react'
import { darkColor, lightColor } from '../Colors'

function TextAreaInput({ label, name }) {
    return (
        <div>
            <label className={`block text-sm font-bold text-${darkColor} dark:text-${lightColor}`} for={name}>{label}</label>
            <textarea name={name} className={`w-full px-5 py-1  focus:outline-none bg-transparent border-${darkColor} dark:border-white border-b-2`}
                id={name} rows="4">
            </textarea>
        </div>
    )
}

export default TextAreaInput