import React from 'react';
import { Routes, BrowserRouter as Router, Route } from 'react-router-dom'
import Footer from './components/Footer';
import Topnav from './components/Topnav';
import Login from './pages/auth/Login';
import Register from './pages/auth/Register';
import ResetPassword from './pages/auth/ResetPassword';
import Cart from './pages/Cart';
import Category from './pages/Category';
import Checkout from './pages/Checkout';
import Home from './pages/Home';
import Offers from './pages/Offers';
import Orders from './pages/Orders';
import ProductDetails from './pages/ProductDetails';
import Store from './pages/Store';

const App = () => {
  return (
    <div>
      <Router>
        <Topnav />
        <Routes>
          <Route path="/login" element={<Login />} exact />
          <Route path="/register" element={<Register />} exact />
          <Route path="/reset/password" element={<ResetPassword />} exact />
          <Route path="/" element={<Home />} exact />
          <Route path="/checkout" element={<Checkout />} exact />
          <Route path="/cart" element={<Cart />} exact />
          <Route path="/store" element={<Store />} exact />
          <Route path="/orders/:order_id" element={<Orders />} exact />
          <Route path="/product/:slug" element={<ProductDetails />} exact />
          <Route path="/offers" element={<Offers />} exact />
          <Route path="/category" element={<Category />} exact />
        </Routes>
        <Footer />
      </Router>
    </div>
  )
}
export default App
