import React from 'react'
import { darkColor, lightColor } from '../Colors'

function TextInput({ label, name, type }) {
  return (
    <div>
      <label className={`block text-sm font-bold text-${darkColor} dark:text-${lightColor}`} for={name}>{label}</label>
      <input className={`w-full px-5 py-1  focus:outline-none bg-transparent border-${darkColor} dark:border-white border-b-2`}
        type={type} id={name} required />
    </div>
  )
}

export default TextInput