import React from 'react'
import { Link } from 'react-router-dom'
import { darkColor, lightColor } from '../Colors'

function LinkButton({link,label}) {
  return (
    <div><Link className={`inline-block right-0 align-baseline font-bold text-sm text-500 text-${darkColor} dark:text-${lightColor} hover:opacity-90`}
    to={link}>{label}</Link></div>
  )
}

export default LinkButton